import request from '@/utils/request'
import { ouchnCustom } from "@/service"

// 查询最新、热门、销量专题列表
export function ListSpecial(query) {
  return request({
    url: ouchnCustom + '/web/subject/querySubjectList',
    method: 'get',
    params: query
  })
}

// 查询专题详情——详细信息
export function specialDetailInfo(subjectId) {
  return request({
    url: ouchnCustom + '/web/subject/getSubjectDetails?subjectId=' + subjectId,
    method: 'get',
  })
}

// 查询专题详情——商品列表
export function listResource(query) {
  return request({
    url: ouchnCustom + '/web/subject/getGoodsBySubjectId',
    method: 'get',
    params: query
  })
}

