// 在线阅读epub——添加笔记
export function insertNote(data) {
  return request({
    url: ouchnCustom + '/web/api/note/addNote',
    method: 'post',
    data: data
  })
}

// 在线阅读epub——修改笔记
export function update(data) {
  return request({
    url: ouchnCustom + '/web/api/note/updateNote',
    method: 'post',
    data: data
  })
}


// 在线阅读epub——删除笔记
export function removeNoteById(noteId) {
  return request({
    url: ouchnCustom + '/web/api/note/deleteNote',
    method: 'post',
    params: { noteId }
  })
}

// 根据资源文件id查询所有笔记
export function selectNoteListByFileId(filesId,type) {
  return request({
    url: ouchnCustom + '/web/api/note/getNoteInfo',
    method: 'get',
    params: { filesId,type }
  })
}




