// 获取征订系统阅读路径
export function getFileLink(data) {
  return request({
    // url: ouchnCustom + '/web/read/getSolicitUserFileLinkAndIsHavingPermissions',
    url: ouchnCustom + '/web/read/getSolicitUserFileLinkAndIsHavingPermissions?goodsId='+data.goodsId+'&goodsFileId='+data.filesId+'&fileType='+data.fileType,
    method: 'get',
    // data: data
  })
}
